      this.mockReturnJSON+" 模拟响应状态:"+this.mockStatus
      if(this.mockNotes){
        addData+=" #   #"+this.mockNotes;
      }
      console.log(addData);

const url ='http://127.0.0.1:8080/mocks';
var params = new URLSearchParams();
params.append('mockPath', this.mockPath);       //你要传给后台的参数值 key/value
params.append('mockMethod', this.mockMethod);
params.append('returnJson',this.mockReturnJSON);
params.append('returnStatus',this.mockStatus);
this.$axios({
    method: 'post',
    url:url,
    data:params
}).then((res)=>{
    
});




      this.items.push({label: addData,isFinished: false})
      this.mockPath="";
      this.mockMethod="";
      this.mockReturnJSON="";
      this.mockMethod="";
    },
    liClick(item){
      item.isFinished = !item.isFinished
    }
  }
}


</script>

<style>
#app{
  width:1000px;
  display: block;
  margin: 0 auto;

}
.finished{
  text-decoration: line-through;
}
#title>li{
  display:inline-block;
  font-size:250px;
}
#title>li:nth-child(1){
  color:HSL(120,20%,40%);
}

#title>li:nth-child(2){
  color:HSL(160,20%,40%);
}

#title>li:nth-child(3){
  color:HSL(180,20%,40%);
}

#title>li:nth-child(4){
  color:HSL(200,60%,40%);
}
</style>



